//Variables
const carrito = document.querySelector("#carrito");
const btnVaciarCarrito = document.querySelector("#vaciar-carrito");
const listaCarrito = document.querySelector("#lista-carrito tbody");
const listaCursos = document.querySelector("#lista-articulos");
let articulosCarrito = []
cargarListeners();

function cargarListeners() {
    //Añadimos un nuevo curso cuando presionamos "Añadir curso"
    listaCursos.addEventListener('click', añadirCurso);

    //Eliminamos un articulo del carrito
    carrito.addEventListener('click',eliminarCurso);

    //Vaciamos el carrito
    btnVaciarCarrito.addEventListener('click',()=>{
        articulosCarrito=[];
        sicronizarStorage();
        limpiarHTML();
    });

    document.addEventListener('DOMContentLoaded',()=>{
        articulosCarrito=JSON.parse(localStorage.getItem('carrito')) || [];
        carritoHTML();
    });

    
}

function añadirCurso(e) {
    e.preventDefault();

    //le damos click al boton de agregar carrito en el articulo
    if (e.target.classList.contains('agregar-carrito')) {
        const cursoSeleccionado = e.target.parentElement.parentElement;

        leerDatosCurso(cursoSeleccionado);
    }
}

function eliminarCurso(e){
    if (e.target.classList.contains('borrar-curso')) {
         const cursoId=e.target.getAttribute('data-id');

         //eliminamos el articulo del carrito
        articulosCarrito=articulosCarrito.filter(curso => curso.id !== cursoId);

        //Volvemos a cargar el html
        carritoHTML();
    }
}

function leerDatosCurso(curso) {
    //console.log(curso);

    const infoCurso = {
        id: curso.querySelector('.agregar-carrito').getAttribute('data-id'),
        imagen: curso.querySelector('img').src,
        titulo: curso.querySelector('h4').textContent,
        precio: curso.querySelector('.precio .oferta').textContent,
        cantidad: 1
    }

    //Revisamos si un elemento ya esta en el carrito
    const existe = articulosCarrito.some(curso => curso.id === infoCurso.id);
    if (existe) {
        //Actualizamos la cantidad
        const cursos = articulosCarrito.map(curso => {
            if (curso.id === infoCurso.id) {
                curso.cantidad++;
                return curso;   //objeto actualizado
            } else {
                return curso;   //objetos no duplicados
            }
        });
    } else {
        //Arregamos el curso al erregglo
        articulosCarrito = [...articulosCarrito, infoCurso];
    }


    carritoHTML();
}


function carritoHTML() {

    //Limpiamos el html previo
    limpiarHTML();
    //Añadimos los cursos al HTML
    articulosCarrito.forEach(curso => {
        const { imagen, titulo, precio, cantidad, id } = curso;
        const row = document.createElement('tr');
        row.innerHTML = `
            <td>
                <img src="${imagen}# width="90">
            </td>
            <td>
                ${titulo}
            </td>
            <td>
                ${precio}
            </td>
            <td>
                ${cantidad}
            </td>
            <td>
                <a href="#" class="borrar-curso" data-id="${id}">X</a>
            </td>
        `;
        //Agrega el html en el carrito
        listaCarrito.appendChild(row);
    });

    //Agregar carrito a local storage
    sicronizarStorage();

}

function sicronizarStorage() {
    localStorage.setItem('carrito',JSON.stringify(articulosCarrito));
}

function limpiarHTML() {
    while (listaCarrito.firstChild) {
        listaCarrito.removeChild(listaCarrito.firstChild);
    }
}


